import React, { useState, useEffect } from "react";
import { Link, navigate } from "gatsby"

import Login, { userCraft } from "../pages/login"
import Logout from "../pages/logout"

import Nav from 'react-bootstrap/Nav'
import ListGroup from 'react-bootstrap/ListGroup'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import FormControl from 'react-bootstrap/FormControl'

import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'

// let usertasks = [
//   { title: "  ", url: "/financial_analysis_layout" },
//   { title: "  ", url: "/beta_calculations" }
// ];

let systemProjects = [
  { title: "  ", url: "/financial_analysis_lab" },
  { title: "  ", url: "/beta_calculations" },
  { title: "", url: "/publication_lab" },
]


function FirebaseAuth(props) {
  const [userCraftLogin, setUserCraftLogin] = useState('authentificating');
  const [userCurrentTask, setUserCurrentTask] = useState(null);
  // const [userTasks, setUserTasks] = useState(null);


  useEffect(() => {
    async function getUserLogin() {
      // You can await here
      let userCraftLogin = await userCraft.getItem('login');
      let userCurrentTask = await userCraft.getItem('currenttask');
      setUserCraftLogin(userCraftLogin);
      setUserCurrentTask(userCurrentTask)
    }
    getUserLogin();
  }, []);

  // console.log(userCraftLogin);

  if (userCraftLogin === 'authentificating') { return <div>...</div> }

  if (userCraftLogin === null) { return <Login setUserCraftLogin={setUserCraftLogin} /> } //setUserTasks={setUserTasks}

  return <div>
    <Nav justify fill className="mb-5">
      {!!userCurrentTask ? <Nav.Item>{userCurrentTask}</Nav.Item> : null}
      <Nav.Item>{userCraftLogin}</Nav.Item>
      <Nav.Item><Logout setUserCraftLogin={setUserCraftLogin} /></Nav.Item>
    </Nav>
    <MyCraft setUserCurrentTask={setUserCurrentTask} />   {/*  userTasks={userTasks} */}
  </div>
}

export default FirebaseAuth

function MyCraft(props) {

  const [userTasks, setUserTasks] = useState(null);
  const [currentTask, setCurrentTask] = useState(null);


  const [show, setShow] = useState(false);
  const [newUserTaskUrl, setNewUserTaskUrl] = useState(null);
  const [title, setTitle] = useState("");
  const [comment, setComment] = useState("");

  useEffect(() => {
    (async () => {

      // const users = await axios.get("https://randomuser.me/api/?page=1&results=10&nat=us");
      // setUsers(users.data.results);

      let userTasks = await userCraft.getItem('usertasks');
      setUserTasks(!!userTasks ? userTasks : []);

      let userCurrentTask = await userCraft.getItem('currenttask');
      setCurrentTask(userCurrentTask);
    })();
  }, [setUserTasks]);



  // useEffect(() => {
  //   async function getUserTasks() {
  //     // You can await here

  //     //let setAllUserTasks = await userCraft.setItem('usertasks', usertasks);
  //     let userTasks = await userCraft.getItem('usertasks');
  //     setUserTasks(!!userTasks ? userTasks : []);

  //     let userCurrentTask = await userCraft.getItem('currenttask');
  //     setCurrentTask(userCurrentTask);
  //   }
  //   getUserTasks();
  // }, []);

  function handleClose() { setShow(false) }

  function saveNewProject() {
    userCraft.setItem('usertasks', [...userTasks, { title: title, comment: comment, url: newUserTaskUrl }])
      .then(res => {
        console.log(res);
        props.setUserCurrentTask(title);
        userCraft.setItem('currenttask', title)
          .then((res) => {
            console.log(res);
            handleClose()
          });
      }
      );
  }

  function doSelectProject(title, url) {
    userCraft.setItem('currenttask', title)
      .then(res => navigate(url))
  }

  // console.log(currentTask, userTasks);

  return <>
    <div className="m-5">{!!userTasks && userTasks.length > 0 ?
      <ListGroup variant="flush" className="mb-5">
        {!!userTasks && userTasks.map(item =>
          <ListGroup.Item key={item.title}
            className={item.title === currentTask ? "bg-light text-dark" : null}
            onClick={() => doSelectProject(item.title, item.url)}
          >
            <div >{item.title}</div>
            <small>{"  "}{item.comment}</small>
          </ListGroup.Item>
        )}
      </ListGroup> : <div>...</div>}</div>

    <Button variant="outline-secondary" onClick={() => setShow(true)} className="mb-5">  </Button>
    <Modal show={show} onHide={handleClose} size='xl' scrollable centered>
      <Modal.Header closeButton>
        <Modal.Title>  </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {!!newUserTaskUrl ?

          <Form className="m-3">
            <Form.Group as={Row} controlId="title">
              <Form.Label column sm="2"></Form.Label>
              <Col sm="10">
                <FormControl
                  onChange={e => setTitle(e.target.value)}
                  aria-label="title"
                  aria-describedby="title"
                  type="text"
                  name="title"
                />
              </Col>
            </Form.Group>

            <Form.Group as={Row} controlId="comment">
              <Form.Label column sm="2"></Form.Label>
              <Col sm="10">
                <Form.Control
                  onChange={e => setComment(e.target.value)}
                  type="text"
                  name="comment" />
              </Col>
            </Form.Group>
          </Form>
          :
          <>
            <div className="m-3"> </div>
            <ListGroup variant="flush" className="mb-5">
              {systemProjects.map(item =>
                <ListGroup.Item key={item.title} onClick={() => setNewUserTaskUrl(item.url)} active={item.url === newUserTaskUrl ? true : false}>
                  {item.title}
                </ListGroup.Item>
              )}
            </ListGroup></>
        }


        {!!newUserTaskUrl && !!title && <Button variant="outline-secondary" onClick={() => saveNewProject()} className="mb-5"></Button>}
      </Modal.Body>
      <Modal.Footer>
      </Modal.Footer>
    </Modal>
  </>

}


