import React from "react";


import useQuotes from "../hooks/useQuotes";

const ScatterChartBetaCompanyLazy = React.lazy(() => import("./ScatterChartBetaCompany.js"))

function BetaRegressionScatterEquation() {
    const data = useQuotes();
    const isSSR = typeof window === "undefined"

    if (!!data?.edges && !isSSR) { return <React.Suspense fallback={<div />}><ScatterChartBetaCompanyLazy data={data.edges} /></React.Suspense> }

    return <div>Beta Regression Scatter Equation</div>
}

export default BetaRegressionScatterEquation;