//import React from "react"
import { useStaticQuery, graphql } from "gatsby"

const useQuotes = () => { //allDataCsv
  const data = useStaticQuery(graphql`
  query MyQuery {
    allMonthlyquotesCsv {
      totalCount
      edges {
        node {
        id
        TICKER
        PER
        DATE
        TIME
        OPEN
        HIGH
        LOW
        CLOSE
        VOL
        }
       }
     }
   }    
  `)
  return data.allMonthlyquotesCsv
}

export default useQuotes;

